.header {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 48px;
  font-family: 'Game of Thrones', sans-serif;
}

.header-container{
  flex: 1;
}

.logo, .logo-subtitle{
  position: relative;
  color: #ac733c;
  font-size: 36px;
  font-weight: 400;
  margin: 0;
  background: -webkit-linear-gradient(top, #E77921, #FCF9B6 50%, #F69E4C);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

.logo:before, .logo-subtitle:before {
  content: attr(data-text);
  position: absolute;
  left: 2px;
  width: 100%;
  color: rgb(7, 3, 3);
  z-index: -2;
}

.logo-subtitle {
  font-size: 16px;
}